import 'rxjs/add/operator/map';

import { Injectable } from '@angular/core';

import { SERMON_LIST } from '../../mocks/sermons.mocks';
import { SermonSeries } from '../../models/sermon.interface';


@Injectable()
export class SermonsProvider {

  public sermonList: Array<SermonSeries> = [];
  public sermonsArray: Array<any> = [];

  constructor() {
    console.log('Hello SermonsProvider Provider');
  }

  loadSermons() {
    return this.sermonList = SERMON_LIST;
  }

  async loadSermon() {
    let result = await this.sermonList.forEach(record => {
      record.sermon.forEach(item => {
        this.sermonsArray.push(item);
        return false;
      });
    });
    return result;
  }

  findSermon(queryText) {
    return this.sermonList.filter((item) => {
      return item.series.toLowerCase().indexOf(queryText.toLowerCase()) > -1;
    });
  }

}




